.control {
    margin: 1.2rem 0;
  
  }
  
  .control label {
    display: block;
    font-weight: bold;
    margin-bottom: 0.5rem;
    text-align: left;
    color:#777;
    
  
  }
  
  .control input,
  .control textarea {
    display: block;
    width: 100%;
    font: inherit;
    padding: 0.2rem;
    border-radius: 12px;
    border: 1px solid #ccc;
    color:#777;
      
    
  }
  
  .control input:focus,
  .control textarea:focus {
    outline: none;
    border-color: #230052;
    color:#777;
    
  }
  .top{
    margin-top: 0rem;
   
    border-radius: 10px;
   
    
  }
  .cancel{
    margin-left: 5rem;
  }
  